body {
  background: linear-gradient(135deg, #6035fd, #66a6ff);
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 620px;
  margin: 50px auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  background: #f7f9fb;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.task-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 300px;

}


.task-name {
  font-size: 20px;
  font-weight: 600;
  color: #222;
}

.timestamp {
  color: #555;
  font-size: 14px;
}

.task-text{
  font-size: 18px;   
  font-weight: 500;  
  color: #222;       
}

.container h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.input-box {
  display: flex;
  gap: 10px;
}

.input-box input {
  flex: 1;
  padding: 10px;
  border: 2px solid #6b9dfc;
  border-radius: 5px;
  outline: none;
}

.input-box input:focus {
  border-color: #1d6dff;
}

.input-box button {
  padding: 10px 15px;
  background: #1d6dff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.input-box button:hover {
  background: #0056e0;
}

.todo-list {
  margin-top: 20px;
  padding-left: 0;
}

.todo-item {
  background: white;
  list-style: none;
  padding: 10px;
  margin-bottom: 8px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-left: 5px solid #1d6dff;
}

.todo-item.completed {
  text-decoration: line-through;
  opacity: 0.6;
  border-left-color: #00a36c;
}

.todo-item span {
  cursor: pointer;
}

.todo-item button {
  background: red;
  color: white;
  border: none;
  padding: 5px 8px;
  border-radius: 3px;
  cursor: pointer;
}

.todo-item button:hover {
  background: darkred;
}
